==============================*/
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  transition: 0.3s; }
  .header__content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 4.8rem;
    position: relative; }
  .header__logo {
    position: relative;
    height: 4rem;
    display: block;
    cursor: pointer; }
    .header__logo object {
      width: 10rem;
      transition: 0.3s; }
    .header__logo object:last-child {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0; }
  .header__nav {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 0;
    background-color: white;
    right: 0;
    transform: translate3d(700px, 0, 0);
    transition: 0.5s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1; }
    .header__nav a {
      font-size: 0.85rem;
      color: #0f1423;
      margin-bottom: 2rem;
      font-family: inherit; }
    .header__nav a:last-child {
      margin-bottom: 0; }
    .header__nav--active {
      width: 100%;
      transform: translate3d(0, 0, 0); }
  .header__social {
