.start {
    .section-core {
        &--start {
            display: flex;
            align-items: center;
            height: 48rem;
            position: relative;

            p {
                max-width: 100%;
            }

            // images
            #platform-5 {
                top: 0.2rem;
            }

            #platform-6 {
                top: -5.5rem;
                left: 5.5rem;
                width: 80%;
            }
        }

        &--choose-fave {
            background-color: $qudos-navyblue;
            background-image: url(../img/core__corporate/choose-fave-bg.jpg);
            background-repeat: no-repeat;
            background-size: 50%;
            background-position: 100% center;
        }

        &--tools-box {
            .row {
                padding-bottom: 5rem;
            }

            a {
                text-decoration: none !important;
                color: $qudos-blue;
            }

            img {
                width: 50%;
            }

            [class^=col-] {
                padding: 7rem 0;
            }

            .col-md-5 {
                background-color: rgba($qudos-lighter-grey, .5);
            }
        }
    }
}

@media (max-width: 1770px) and (min-width: 1200px) {
    .start {
        .section-core {
            &--start {
                #platform-6 {
                    top: -7.5rem;
                    left: 6.5rem;
                }
            }
        }
    }
}

@media (max-width: 1475px) {
    .start {
        .section-core {
            &--choose-fave {
                background-size: contain;
            }
        }
    }
}

@media (max-width: 1199px) {
    .start {
        .section-core {
            &--choose-fave {
                background-position-x: 40rem;
            }
        }
    }
}

@media (max-width: 1080px) {
    .start {
        .section-core {
            &--choose-fave {
