    .start {
        .section-core {
            &--start {
                #platform-6 {
                    top: -2.7rem;
                    left: 4.5rem;
                }
            }
    
            &--choose-fave {
                background-image: url(../img/core__corporate/choose-fave-bg@991.jpg);
                background-position-x: right;
            }
        }
    }
}

@media (max-width: 935px) {
    .start {
        .section-core {
            &--choose-fave {
                background-position-x: 35rem;
            }
        }
    }
}

