@media (max-width: 767px) {
    .start {
        .section-core {
            &--start {
                overflow: hidden;
                margin-top: 5rem;
    
                #platform-6 {
                    top: -4.8rem;
                    left: 5.5rem;
                }
    
                .core__parag {
                    max-width: 60%;
                    min-width: 50%;
                }
    
                .col-sm-12:first-child {
                    &>span {
                        left: 24rem;
                    }
                }
            }
    
            &--portal {
                .d-block {
                    display: inline !important;
                }
    
                .col-12:first-child {
                    padding-top: 0;
                }
            }
    
            &--live-map {
                [class^=col-] {
                    padding: 5rem 0;
                }
    
                .col-sm-12:last-child {
                    padding-top: 0;
                }
            }
    
            &--choose-fave {
                background-image: url(../img/core__corporate/choose-fave-bg@767.jpg);
                background-position: center bottom;
    
                .col-sm-12:first-child {
                    padding-top: 5rem;
                }
            }
    
            &--tools-box {
                .col-sm-12:first-child {
                    order: 1;
                    padding-top: 5rem;
                    justify-content: flex-start !important;
                }
    
                .col-sm-12:last-child {
                    padding-bottom: 5rem;
                    order: 2;
    
                }
            }
        }
    }
}

@media (max-width: 575px) {
    .start {
        .section-core {
            &--live-map {
                [class^=col-] {
                    padding-left: 1rem;
                    padding-right: 1rem;
                }
            }
    
            &--choose-fave {
                [class^=col-] {
                    padding-left: 1rem;
                    padding-right: 1rem;
                }
    
                .col-sm-12:first-child {
                    padding-bottom: 3rem;
                }
            }
    
            &--tools-box {
                [class^=col-] {
                    padding-left: 1rem !important;
                    padding-right: 1rem !important;
                }
            }
        }
    }
}

@media (max-width: 425px) {
    .start {
        .section-core {
            &--start {
                overflow: visible;
    
                #platform-6 {
                    top: -3.10rem;
                    left: 4.5rem;
                }
    
                .col-sm-12:first-child {
                    &>span {
                        top: -9rem;
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }
    
                .col-sm-12:last-child {
                    text-align: center;
    
                    &>span {
                        margin-top: 2rem;
                        position: relative;
                        top: 10rem;
                    }
    
                    h1 {
                        text-align: center;
    
                        &::after {
                            left: 50%;
                            transform: translateX(-50%);
                        }
                    }
    
                    p {
                        max-width: 100%;
                        min-width: 100%;
                        text-align: center;
                    }
                }
            }
    
            &--live-map {
    
                h2,
                p {
                    text-align: center;
                }
            }
    
            &--portal {
                padding-top: 10rem;
            }
    
            &--choose-fave {
    
                h2,
                p {
                    text-align: center;
                }
    
                p {
                    margin-left: auto;
                    margin-right: auto;
                    max-width: 90%;
                }
    
                .col-sm-12:first-child {
                    padding-bottom: 0;
                }
            }
    
            &--tools-box {
                .col-sm-12:first-child {
                    justify-content: center !important;
                }
    
                .col-sm-12:last-child {
                    padding-top: 5rem;
                    padding-bottom: 2rem;
    
                    h2,
                    p {
                        text-align: center;
                    }
    
                    p {
                        margin-left: auto;
                        margin-right: auto;
                        max-width: 80%;
                    }
                }
            }
        }
    }
}

